<template>
  <div class="checkout-page">
    <section v-if="cart.empty" class="empty-cart">
      <div class="empty-cart-img"></div>
      <span class="empty-cart-msg">Your cart is empty !</span>
      <router-link :to="'./category/' + $store.state.selectedCategoryName">
        <div class="buttons secondary-button">Browse Categories</div>
      </router-link>
    </section>
    <div class="cart-checkout" v-else>
      <span class="title">Shipping Details</span>
      <form @submit.prevent="submitOrder">
        <div class="input txt">
          <label for="name" class="label">Name</label>
          <input
            type="text"
            id="name"
            size="20"
            v-model.lazy="$v.name.$model"
          />
        </div>
        <template v-if="$v.name.$error">
          <span class="error" v-if="!$v.name.required">Name is required</span>
          <span class="error" v-else-if="!$v.name.minLength">
            Name must have at least
            {{ $v.name.$params.minLength.min }} letters.
          </span>
          <span class="error" v-else-if="!$v.name.maxLength">
            Name can have at most
            {{ $v.name.$params.maxLength.max }} letters.
          </span>
          <span class="error" v-else> An unexpected error occurred. </span>
        </template>

        <div class="input txt">
          <label for="address" class="label">Address</label>
          <input
            type="text"
            id="address"
            size="20"
            v-model.lazy="$v.address.$model"
          />
        </div>
        <template v-if="$v.address.$error">
          <span class="error" v-if="!$v.address.required"
            >Address is required</span
          >
          <span class="error" v-else-if="!$v.address.minLength">
            Address must have at least
            {{ $v.address.$params.minLength.min }} letters.
          </span>
          <span class="error" v-else-if="!$v.address.maxLength">
            Address can have at most
            {{ $v.address.$params.maxLength.max }} letters.
          </span>
          <span class="error" v-else> An unexpected error occurred. </span>
        </template>

        <div class="input txt">
          <label for="phone" class="label">Phone</label>
          <input
            type="text"
            id="phone"
            size="20"
            v-model.lazy="$v.phone.$model"
          />
        </div>
        <template v-if="$v.phone.$error">
          <span class="error" v-if="!$v.phone.required">Phone is required</span>
          <span class="error" v-else-if="!$v.phone.phone">
            Please enter a valid phone number.
          </span>
          <span class="error" v-else> An unexpected error occurred. </span>
        </template>

        <div class="input txt">
          <label for="email" class="label">Email</label>
          <input
            type="text"
            id="email"
            size="20"
            v-model.lazy="$v.email.$model"
          />
        </div>
        <template v-if="$v.email.$error">
          <span class="error" v-if="!$v.email.required">Email is required</span>
          <span class="error" v-else-if="!$v.email.email">
            Please enter a valid email address.
          </span>
          <span class="error" v-else> An unexpected error occurred. </span>
        </template>

        <div class="input txt">
          <label for="ccNumber" class="label">Credit Card</label>
          <input
            type="text"
            id="ccNumber"
            size="20"
            v-model.lazy="$v.ccNumber.$model"
          />
        </div>
        <template v-if="$v.ccNumber.$error">
          <span class="error" v-if="!$v.ccNumber.required"
            >Credit Card is required</span
          >
          <span class="error" v-else-if="!$v.ccNumber.ccNumber">
            Please enter a valid Credit Card Number.
          </span>
          <span class="error" v-else> An unexpected error occurred. </span>
        </template>

        <div class="input">
          <label class="label">Expiry Date</label>
          <select v-model="ccExpiryMonth">
            <option
              v-for="(month, index) in months"
              :key="index"
              :value="index + 1"
            >
              {{ month }}
            </option>
          </select>
          <select v-model="ccExpiryYear">
            <option v-for="(year, index) in 15" :key="index">
              {{ yearFrom(index) }}
            </option>
          </select>
        </div>

        <div class="checkoutMsg">
          <div>
            <label for="">SUBTOTAL : </label
            >{{ cart.subtotal | asDollarsAndCents }}
          </div>
          <div>
            <label for="">SURCHARGE : </label
            >{{ cart.surcharge | asDollarsAndCents }}
          </div>
          <div>
            <label for="">TOTAL : </label
            >{{ (cart.subtotal + cart.surcharge) | asDollarsAndCents }}
          </div>
        </div>
        <input
          type="submit"
          name="submit"
          class="buttons input primary-button"
          :disabled="checkoutStatus == 'PENDING'"
          value="Complete Purchase"
        />
      </form>

      <section v-show="checkoutStatus != ''" class="checkoutStatusBox">
        <div v-if="checkoutStatus == 'ERROR'">
          Please fix the problems above and try again.
        </div>

        <div v-else-if="checkoutStatus == 'PENDING'">Processing...</div>

        <div v-else-if="checkoutStatus == 'OK'">Order placed...</div>

        <div v-else>An unexpected error occurred, please try again.</div>
      </section>
    </div>
  </div>
</template>

<script>
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import isCreditCard from "validator/lib/isCreditCard";
import isMobilePhone from "validator/lib/isMobilePhone";

const phone = (value) => isMobilePhone(value, "en-US");
const creditCard = (value) => isCreditCard(value);

export default {
  name: "Checkout",
  data() {
    return {
      name: "",
      address: "",
      phone: "",
      email: "",
      ccNumber: "",
      ccExpiryMonth: new Date().getMonth() + 1,
      ccExpiryYear: new Date().getFullYear(),
      checkoutStatus: "",
    };
  },
  validations: {
    name: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    address: {
      required,
      minLength: minLength(4),
      maxLength: maxLength(45),
    },
    phone: {
      required,
      phone,
    },
    email: {
      required,
      email,
    },
    ccNumber: {
      required,
      ccNumber: creditCard,
    },
  },

  computed: {
    cart() {
      return this.$store.state.cart;
    },
    months() {
      return [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];
    },
  },

  methods: {
    submitOrder() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.checkoutStatus = "ERROR";
      } else {
        this.checkoutStatus = "PENDING";
        this.$store
          .dispatch("placeOrder", {
            name: this.name,
            address: this.address,
            phone: this.phone,
            email: this.email,
            ccNumber: this.ccNumber,
            ccExpiryMonth: this.ccExpiryMonth,
            ccExpiryYear: this.ccExpiryYear,
          })
          .then(() => {
            this.checkoutStatus = "OK";
            this.$router.push({ name: "confirmation" });
          })
          .catch((reason) => {
            this.checkoutStatus = "SERVER_ERROR";
            console.log("Error placing order", reason);
          });
      }
    },

    yearFrom(index) {
      return new Date().getFullYear() + index;
    },
  },
};
</script>

