<template>
  <div class="cart-table">
    <ul>
      <li
        v-for="item in $store.state.orderDetails.lineItems"
        :key="item.bookId"
        class="order-item"
      >
        <div class="item-details">
          <div class="book-image">
            <img
              :src="require(`@/assets/images/books/` + bookImageFileName(item))"
              alt="book image"
            />
          </div>
          <div class="book-title">
            {{ getBookOfItem(item).title }} by {{ getBookOfItem(item).author }}
          </div>
          <div class="book-price">${{ getBookOfItem(item).price }}</div>
          <div class="item-quantity">Qty : {{ item.quantity }}</div>
          <div class="item-subtotal">
            Item Total : ${{ getBookOfItem(item).price * item.quantity }}.00
          </div>
        </div>
        <hr />
      </li>
    </ul>
    <div class="checkoutMsg">
      <div>
        <label for="">SUBTOTAL : </label>{{ getSubTotal() | asDollarsAndCents }}
      </div>
      <div>
        <label for="">SURCHARGE : </label
        >{{
          ($store.state.orderDetails.order.amount - getSubTotal())
            | asDollarsAndCents
        }}
      </div>
      <div>
        <label for="">TOTAL : </label
        >{{ $store.state.orderDetails.order.amount | asDollarsAndCents }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ConfirmationTable",
  methods: {
    bookImageFileName: function (item) {
      for (let book of this.$store.state.orderDetails.books) {
        if (book.bookId == item.bookId) {
          let name = book.title.toLowerCase();
          name = name.replace(/ /g, "-");
          return `${name}.jpg`;
        }
      }
    },
    getBookOfItem: function (item) {
      for (let book of this.$store.state.orderDetails.books) {
        if (book.bookId == item.bookId) return book;
      }
    },
    getSubTotal: function () {
      let subtotal = 0;
      for (let item of this.$store.state.orderDetails.lineItems) {
        for (let book of this.$store.state.orderDetails.books) {
          if (book.bookId == item.bookId) {
            subtotal += book.price * item.quantity;
          }
        }
      }
      return subtotal;
    },
  },
};
</script>

