<template>
  <div class="cart-table">
    <ul>
      <li
        v-for="item in $store.state.cart.items"
        :key="item.book.bookId"
        class="order-item"
      >
        <div class="item-details">
          <div class="book-image">
            <img
              :src="
                require(`@/assets/images/books/` + bookImageFileName(item.book))
              "
              alt="book image"
            />
          </div>
          <div class="book-title">
            {{ item.book.title }} by {{ item.book.author }}
          </div>
          <div class="book-price">${{ item.book.price }}</div>
          <div class="item-quantity">
            <i
              class="quantity-update-button fas fa-minus-circle"
              @click="updateCart(item.book, item.quantity - 1)"
            ></i>
            {{ item.quantity }}
            <i
              class="quantity-update-button fas fa-plus-circle"
              @click="updateCart(item.book, item.quantity + 1)"
            ></i>
          </div>
          <div class="item-subtotal">
            Item Total : ${{ item.book.price * item.quantity }}.00
          </div>
        </div>
        <hr />
        <div class="delete-item" @click="updateCart(item.book, 0)">
          <i class="fas fa-trash-alt"> </i>
          Delete
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "CartTable",
  methods: {
    bookImageFileName: function (book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ /g, "-");
      return `${name}.jpg`;
    },
    updateCart: function (book, quantity) {
      this.$store.dispatch("updateCart", { book, quantity });
    },
  },
};
</script>

