  display: flex;
  justify-content: flex-end;
  cursor: pointer;
}

li.order-item {
  box-shadow: 0px 0px 4px #000;
  margin: 1em;
  margin-left: 0;
}

i.quantity-update-button {
  cursor: pointer;
}
