<template>
  <div class="category">
    <category-nav></category-nav>
    <category-book-list :books="$store.state.selectedCategoryBooks">
    </category-book-list>
  </div>
</template>

<script>
import CategoryNav from "@/components/CategoryNav";
import CategoryBookList from "@/components/CategoryBookList";

export default {
  name: "category",
  components: {
    CategoryNav,
    CategoryBookList,
  },
  created: function () {
    const self = this;
    this.$store
      .dispatch("fetchBooksByCategoryName", this.$route.params.name)
      .catch(() => {
        self.$router.push("/404");
      });
  },
  watch: {
    $route: "fetchBooksByCategory",
  },
};
</script>

<style scoped>
.category {
  display: flex;
  height: 46rem;
}
</style>
