<template>
  <div class="book">
    <div class="book-image" :class="book.bookId">
      <img
        :src="require(`@/assets/images/books/` + bookImageFileName(book))"
        alt="book image"
      />
      <div v-if="book.isPublic" class="read-now">Read Now</div>
    </div>
    <div class="book-title" :title="book.title">
      {{ book.title }}
    </div>
    <div class="book-author">by {{ book.author }}</div>
    <div class="book-price">
      <span class="dollar">{{ book.price | asDollarsAndCents }}</span>
    </div>
    <div class="add-to-cart" @click="addToCart(book)">
      <i class="fas fa-shopping-cart"></i> Add to Cart
    </div>
  </div>
</template>

<script>
export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true,
    },
  },
  methods: {
    bookImageFileName: function (book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ /g, "-");
      return `${name}.jpg`;
    },
    addToCart: function (book) {
      this.$store.dispatch("addToCart", book);
    },
  },
};
</script>
