import Vue from "vue";
import Vuex from "vuex";
import ApiService from "./services/ApiService";
import constants from "./util/constants";
import { ShoppingCart } from "./models/ShoppingCart";

Vue.use(Vuex);

export const CART_STORAGE_KEY = "cart";

export default new Vuex.Store({
  state: {
    categories: [],
    selectedCategoryName: "",
    selectedCategoryBooks: [],
    cart: new ShoppingCart(),
    orderDetails: {},
  },
  mutations: {
    SET_CATEGORIES(state, newCategories) {
      state.categories = newCategories;
    },
    SET_SELECTED_CATEGORY_NAME(state, categoryName) {
      state.selectedCategoryName = categoryName;
    },
    SET_CATEGORY_BOOKS(state, books) {
      state.selectedCategoryBooks = books;
    },
    ADD_TO_CART(state, book) {
      state.cart.addItem(book, 1);
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(this.state.cart));
    },
    UPDATE_CART(state, { book, quantity }) {
      state.cart.update(book, quantity);
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(this.state.cart));
    },
    CLEAR_CART(state) {
      state.cart.clear();
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(this.state.cart));
    },
    SET_CART(state, shoppingCart) {
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(shoppingCart));
      let newCart = new ShoppingCart();
      shoppingCart.items.forEach((item) => {
        newCart.addItem(item.book, item.quantity);
      });
      state.cart = newCart;
    },
    SET_ORDER_DETAILS(state, orderDetails) {
      state.orderDetails = orderDetails;
    },
  },
  actions: {
    async fetchCategories(context) {
      try {
        const categories = await ApiService.fetchData("/categories");
        context.commit("SET_CATEGORIES", categories);
      } catch (error) {
        console.log(
          "Error occured in fetchCategories in store. Error : " + error
        );
      }
    },
    async fetchBooksByCategoryName(context, categoryName) {
      try {
        if (this.state.selectedCategoryName != categoryName) {
          let books = await ApiService.fetchData(
            "/categories/name/" + categoryName + "/books"
          );
          books = books.map((book) => {
            book.bookImage = constants.bookImageFileName(book);
            return book;
          });
          context.commit("SET_CATEGORY_BOOKS", books);
          context.commit("SET_SELECTED_CATEGORY_NAME", categoryName);
        }
      } catch (error) {
        console.log("Error in fetchBooksByCategory " + error);
        throw new Error(error);
      }
    },
    addToCart(context, book) {
      context.commit("ADD_TO_CART", book);
    },
    updateCart(context, { book, quantity }) {
      context.commit("UPDATE_CART", { book, quantity });
    },
    clearCart(context) {
      context.commit("CLEAR_CART");
    },
    placeOrder({ commit, state }, customerForm) {
      return ApiService.placeOrder({
        cart: state.cart,
        customerForm: customerForm,
      }).then((response) => {
        commit("CLEAR_CART");
        commit("SET_ORDER_DETAILS", response);
      });
    },
  },
});
